import React, { Component } from 'react';
import CMI from '../../components/TrainingType/CMI';
import CAI from '../../components/TrainingType/CAI';
import Live from '../../components/TrainingType/Live';
import Fields from '../../components/TrainingType/Fields';

import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Divider from '@material-ui/core/Divider'
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment'
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@material-ui/core/Tooltip';

import appText from '../../data/appText';

const styles = theme => ({
    root: {
        padding: theme.spacing.unit * 2,
        width: '100%'
    },
    paper: {
        padding: theme.spacing.unit * 2
    },
    textField: {
        marginBottom: theme.spacing.unit
    },
    divider: {
        marginTop: theme.spacing.unit * 2,
        marginBottom: theme.spacing.unit * 2
    }
})

class CostCalculator extends Component {    
    render () {
        const { classes }  = this.props;
        let selectedTraining = null;
        switch (this.props.state.selected) {
            case 'cmi':
                selectedTraining = <CMI data={this.props.state} />
                break;
            case 'cai':
                selectedTraining = <CAI data={this.props.state} />
                break;
            case 'live':
                selectedTraining = <Live data={this.props.state} />
                break;
            default:
                selectedTraining = <Typography variant="body1">Enter shared field values and select a method of instruction in the panel to the left.</Typography>;
                break;
        }
        
        return (
            <Grid container spacing={8} className={classes.root} style={{flexWrap: 'nowrap'}}>
                <Grid item xs={12} lg={3}>
                    <Paper className={classes.paper}>
                        <Typography variant="h5" gutterBottom>Shared Data</Typography>
                        <Typography variant="body1" gutterBottom>Enter shared data values in the fields below:</Typography>

                        <TextField 
                            type="number" id="shared-learnerCostPerDay" fullWidth
                            className={classes.textField}
                            label={appText.shared.fields.dailyClassroomCostPerLearner.label}
                            onChange={this.props.handleFieldChange} value={this.props.state.shared.learnerCostPerDay} 
                            InputProps={{
                                startAdornment: <InputAdornment position="start">$</InputAdornment>
                              }}/>
                        
                        <Tooltip title="Loaded cost includes salary and other benefits." enterDelay={1000}>
                        <TextField 
                            type="number" id="shared-loadedAnnualInstructorCost" fullWidth
                            className={classes.textField}
                            label={appText.shared.fields.annualLoadedCostPerInst.label}
                            onChange={this.props.handleFieldChange} value={this.props.state.shared.loadedAnnualInstructorCost}
                            InputProps={{
                                startAdornment: <InputAdornment position="start">$</InputAdornment>
                              }}/>
                        </Tooltip>

                        <TextField 
                            type="number" id="shared-TDYCostPerDay" fullWidth
                            className={classes.textField}
                            label={appText.shared.fields.dailyTDYCostPerLearner.label}
                            onChange={this.props.handleFieldChange} value={this.props.state.shared.TDYCostPerDay}
                            InputProps={{
                                startAdornment: <InputAdornment position="start">$</InputAdornment>
                              }} />

                        <TextField 
                            type="number" id="shared-learnerTargetAudience" fullWidth
                            className={classes.textField}
                            label={appText.shared.fields.totalTargetAudienceThroughput.label}
                            onChange={this.props.handleFieldChange} value={this.props.state.shared.learnerTargetAudience} />

                        <Divider className={classes.divider}/>

                        <Typography variant="h5" gutterBottom>Method of Instruction Data</Typography>
                        <Typography variant="body1"gutterBottom>Select a method of instruction and enter values in the fields below:</Typography>
                        <RadioGroup
                            name="training-type"
                            value={this.props.state.selected}
                            onChange={this.props.handleTrainingType}
                            row>
                              <FormControlLabel value="cmi" control={<Radio color="primary"/>} label="CMI"/>
                              <FormControlLabel value="cai" control={<Radio color="primary"/>} label="CAI"/>
                              <FormControlLabel value="live" control={<Radio color="primary"/>} label="Live"/>
                        </RadioGroup>

                        <Fields 
                            data={this.props.state} 
                            onChanged={this.props.handleFieldChange} 
                            onIMILvlChange={this.props.handleIMILevelChange}
                            onCheckboxChanged={this.props.handleFieldCheckboxChange}/>
                    </Paper>
                </Grid>
                <Grid item xs={12} lg={9}>
                    <Paper className={classes.paper}>
                        {selectedTraining}
                    </Paper>
                </Grid>
            </Grid>
        );
    }
}

export default withStyles(styles)(CostCalculator);