import React, { Component, Fragment } from 'react';

import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell'
import TextField from '@material-ui/core/TextField';
import Divider from '@material-ui/core/Divider';
import InputAdornment from '@material-ui/core/InputAdornment';
import { Select, FormControl, MenuItem } from '@material-ui/core';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';

import appText from '../../../data/appText';

const styles = theme => ({
    root: {
        padding: theme.spacing.unit * 2,
        width: '100%'
    },
    paper: {
        padding: theme.spacing.unit * 2
    },
    tableRow: {
        height: '40px'
    },
    tableCell: {
        borderBottom: 0,
        padding: 0,
        textAlign: 'left'
    },
    divider: {
        marginTop: theme.spacing.unit * 2,
        marginBottom: theme.spacing.unit * 2
    }
});

class CMI extends Component {
    constructor(props) {
        super(props);
        this.data = {
            ...this.props.data.shared,
            ...this.props.data.cmi,
            costPerIMIHour: 25000,
            TADLPContractCost: 0,
            devCostPerLearner: 0,
            GARLaborRate: 18.03,
            GARTime: 0,
            GARCost: 0,
            GARCostPerLearner: 0,
            FY14ALMSCost: 1.86,
            FTFieldingCost: 0
        }
    }

    calcAndSetData() {
        let workingData = { ...this.data, ...this.props.data.shared, ...this.props.data.cmi };

        workingData.contractIMIHours = workingData.finishedIMIHours * workingData.imiMultiplier - workingData.finishedIMIHours;

        let calculations = {
            costPerIMIHour: Number(workingData.costPerIMIHour),
            TADLPContractCost: workingData.finishedIMIHours * workingData.costPerIMIHour,
            devCostPerLearner: (workingData.finishedIMIHours * workingData.costPerIMIHour) / workingData.learnerTargetAudience,
            GARTime: workingData.finishedIMIHours * 1.6,
            GARCost: (workingData.finishedIMIHours * 1.6) * workingData.GARLaborRate,
            GARCostPerLearner: ((workingData.finishedIMIHours * 1.6) * workingData.GARLaborRate) / workingData.learnerTargetAudience,
            FTFieldingCost: workingData.FY14ALMSCost * workingData.contentPackages,
            ImplementationCostPerLearner: (workingData.FY14ALMSCost * workingData.contentPackages) / workingData.learnerTargetAudience,
            athdCost: Number(workingData.cmiAthdCost),
            athdLicenseCost: Number(workingData.cmiAthdLicenseCost),
            tktHours: workingData.cmiTktHours,
            datrCost: Number(workingData.cmiDatrCost),
            evaluationCostPerLearner: (Number(workingData.cmiAthdCost) + Number(workingData.cmiAthdLicenseCost) + Number(workingData.cmiDatrCost)) / workingData.learnerTargetAudience,
            evaluationCostPerIMIHour: (Number(workingData.cmiAthdCost) + Number(workingData.cmiAthdLicenseCost) + Number(workingData.cmiDatrCost)) / workingData.finishedIMIHours,
            TotalCost: 
                (workingData.finishedIMIHours * workingData.costPerIMIHour) 
                + (workingData.finishedIMIHours * 1.6) * workingData.GARLaborRate
                + (Number(workingData.cmiAthdCost) + Number(workingData.cmiAthdLicenseCost) + Number(workingData.cmiDatrCost)),
            TotalCostPerLearner: 
                ((workingData.finishedIMIHours * workingData.costPerIMIHour) / workingData.learnerTargetAudience) 
                + (((workingData.finishedIMIHours * 1.6) * workingData.GARLaborRate) / workingData.learnerTargetAudience) 
                + ((workingData.FY14ALMSCost * workingData.contentPackages) / workingData.learnerTargetAudience)
                + ((Number(workingData.cmiAthdCost) + Number(workingData.cmiAthdLicenseCost) + Number(workingData.cmiDatrCost)) / workingData.learnerTargetAudience)
        }

        this.data = { ...workingData, ...calculations }        
    }

    render() {
        const { classes } = this.props;

        this.calcAndSetData();

        const cmiSpacerRow = this.props.data.imiLevel !== "1" ?
            <TableRow className={classes.tableRow}>
                <TableCell className={classes.tableCell}></TableCell>
                <TableCell className={classes.tableCell}></TableCell>
            </TableRow> : null;

        const otherIMICostField = this.props.data.imiLevel === "1" ? 
            <TableRow className={classes.tableRow}>
                <TableCell className={classes.tableCell}>
                    {appText.cmi.fields.otherIMICost.label}
                </TableCell>
                <TableCell className={classes.tableCell}>
                    <TextField 
                        type="number" id="cmi-customCostPerIMIHour" fullWidth className={classes.textField}
                        onChange={this.props.handleFieldChange} value={this.data.customCostPerIMIHour} 
                        InputProps={{
                            startAdornment: <InputAdornment position="start">$</InputAdornment>
                        }}/>
                </TableCell>
            </TableRow>
             : null;

        return (
            <Paper className={classes.paper}>
                <Typography variant="h6" gutterBottom>{appText.cmi.title}</Typography>
                <Typography variant="body1"gutterBottom>{appText.cmi.description}</Typography>
                <Divider className={classes.divider} />
                <Table>
                    <TableBody>
                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <Typography variant="h6">CMI Data Fields</Typography>
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                IMI Level (Army / Others)
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                <FormControl style={{width: '100%'}}>
                                    <Select
                                        value={this.props.data.imiLevel}
                                        onChange={this.props.handleIMILevelChange}>
                                        <MenuItem value="1">0 (1)</MenuItem>
                                        <MenuItem value="2">1 (2)</MenuItem>
                                        <MenuItem value="3">2 (3)</MenuItem>
                                        <MenuItem value="4">3 (4)</MenuItem>
                                        {/* <MenuItem value="0">Other</MenuItem> */}
                                    </Select>
                                </FormControl>
                            </TableCell>
                        </TableRow>
                        {otherIMICostField}
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.fields.developedIMIHours.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                <TextField 
                                    type="number" id="cmi-finishedIMIHours" fullWidth
                                    className={classes.textField}
                                    onChange={this.props.handleFieldChange} value={this.data.finishedIMIHours} />
                            </TableCell>
                        </TableRow>
                        {/* <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.fields.contractIMIHours.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                <TextField 
                                    type="number" id="cmi-contractIMIHours" fullWidth
                                    className={classes.textField}
                                    onChange={this.props.handleFieldChange} value={this.data.contractIMIHours} />
                            </TableCell>
                        </TableRow> */}
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.fields.contentPackages.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                <TextField 
                                    type="number" id="cmi-contentPackages" fullWidth
                                    className={classes.textField}
                                    onChange={this.props.handleFieldChange} value={this.data.contentPackages} />
                            </TableCell>
                        </TableRow>
                        {cmiSpacerRow}

                        <TableRow>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <FormControlLabel 
                                    label = "Enter Evaluation field values"
                                    control = {
                                        <Checkbox
                                            id="cmi-enterEvalFields"
                                            checked={this.data.enterEvalFields}
                                            onChange={this.props.handleFieldCheckboxChange}/>
                                    }/>
                            </TableCell>
                        </TableRow>
                        {this.data.enterEvalFields ? 
                            <Fragment>
                                <TableRow className={classes.tableRow}>
                                    <TableCell colSpan={2} className={classes.tableCell}>
                                        <Typography variant="h6">Evaluation Fields (for future use)</Typography>
                                    </TableCell>
                                </TableRow>
                                <TableRow className={classes.tableRow}>
                                    <TableCell className={classes.tableCell}>
                                        {appText.shared.fields.evaluation.helpDeskCost.label}
                                    </TableCell>
                                    <TableCell className={classes.tableCell}>
                                        <TextField 
                                            type="number" id="cmi-cmiAthdCost" fullWidth
                                            className={classes.textField}
                                            onChange={this.props.handleFieldChange} value={this.data.cmiAthdCost} />
                                    </TableCell>
                                </TableRow>
                                <TableRow className={classes.tableRow}>
                                    <TableCell className={classes.tableCell}>
                                        {appText.shared.fields.evaluation.helpDeskLicenseCost.label}
                                    </TableCell>
                                    <TableCell className={classes.tableCell}>
                                        <TextField 
                                            type="number" id="cmi-cmiAthdLicenseCost" fullWidth
                                            className={classes.textField}
                                            onChange={this.props.handleFieldChange} value={this.data.cmiAthdLicenseCost} />
                                    </TableCell>
                                </TableRow>
                                <TableRow className={classes.tableRow}>
                                    <TableCell className={classes.tableCell}>
                                        {appText.shared.fields.evaluation.tktHours.label}                            
                                    </TableCell>
                                    <TableCell className={classes.tableCell}>
                                        <TextField 
                                            type="number" id="cmi-cmiTktHours" fullWidth
                                            className={classes.textField}
                                            onChange={this.props.handleFieldChange} value={this.data.cmiTktHours} />
                                    </TableCell>
                                </TableRow>
                                <TableRow className={classes.tableRow}>
                                    <TableCell className={classes.tableCell}>
                                        {appText.shared.fields.evaluation.datrCost.label}
                                    </TableCell>
                                    <TableCell className={classes.tableCell}>
                                        <TextField 
                                            type="number" id="cmi-cmiDatrCost" fullWidth
                                            className={classes.textField}
                                            onChange={this.props.handleFieldChange} value={this.data.cmiDatrCost} />
                                    </TableCell>
                                </TableRow>
                            </Fragment> : null}                      
                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <Divider className={classes.divider}/>
                                <Typography variant="h5" gutterBottom>Costs by ADDIE Stage</Typography>
                                <Typography variant="h6" gutterBottom>{appText.misc.analysis.title}</Typography>   
                                <Typography variant="body1" gutterBottom>{appText.misc.analysis.text}</Typography>  
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <Typography variant="h6">{appText.misc.design.title}</Typography>   
                                <Typography variant="body1" gutterBottom>{appText.misc.design.text}</Typography>
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <Typography variant="h6">{appText.misc.development.title}</Typography>
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.fields.developedIMIHours.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                {this.data.finishedIMIHours}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.fields.contractIMIHours.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                {this.data.contractIMIHours}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.development.costPerIMIHour.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.costPerIMIHour.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.shared.fields.totalTargetAudienceThroughput.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                {this.data.learnerTargetAudience.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.development.contractCost.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.TADLPContractCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.development.costPerLearner.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.devCostPerLearner.toFixed(2)}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <Typography variant="h6">{appText.misc.implementation.title}</Typography>
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.implementation.govtAcceptanceLaborRate.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.GARLaborRate}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.implementation.hoursToCompleteAcceptance.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                {this.data.GARTime.toFixed(0)}</TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.implementation.govtAcceptanceCost.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.GARCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.implementation.govtAcceptanceCostPerLearner.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.GARCostPerLearner.toFixed(4)}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.implementation.lmsCostPerUnit.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.FY14ALMSCost}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.fields.contentPackages.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                {this.data.contentPackages}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.implementation.ftAndFieldingCostPerLearner.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.FTFieldingCost.toFixed(2)}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.implementation.costPerLearner.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.ImplementationCostPerLearner.toFixed(4)}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}></TableCell>
                            <TableCell className={classes.tableCell}></TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <Typography variant="h6">{appText.misc.evaluation.title}</Typography>
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.shared.fields.evaluation.helpDeskCost.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.athdCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.shared.fields.evaluation.helpDeskLicenseCost.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.athdLicenseCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.shared.fields.evaluation.tktHours.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                {this.data.tktHours.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.shared.fields.evaluation.datrCost.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.datrCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.evaluation.costPerLearner.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.evaluationCostPerLearner.toFixed(4)}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.evaluation.costPerIMIHour.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.evaluationCostPerIMIHour.toFixed(2)}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <Typography variant="h6">{appText.misc.totals.title}</Typography>
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.totals.cost.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.TotalCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.totals.costPerLearner.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.TotalCostPerLearner.toFixed(2)}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.totals.costPerLearner3Year.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${(this.data.TotalCostPerLearner * 2).toFixed(2)}
                            </TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
            </Paper>
        );
    }
}

export default withStyles(styles)(CMI);