import React, { Fragment } from 'react';

import { withStyles, Typography } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import Checkbox from '@material-ui/core/Checkbox';

import appText from '../../../data/appText';

const styles = theme => ({
    root: {
        padding: theme.spacing.unit * 2,
        width: '100%'
    },
    paper: {
        padding: theme.spacing.unit * 2
    },
    note: {
        marginBottom: theme.spacing.unit * 2
    },
    textField: {
        marginBottom: theme.spacing.unit
    },
    divider: {
        marginTop: theme.spacing.unit * 2,
        marginBottom: theme.spacing.unit * 2
    }
});

function Fields(props) {
    const {classes} = props;
    const otherIMICostField = props.data.imiLevel === "1" ? 
        <TextField 
            type="number" id="cmi-customCostPerIMIHour" fullWidth className={classes.textField}
            label={appText.cmi.fields.otherIMICost.label}
            onChange={props.onChanged} value={props.data.cmi.customCostPerIMIHour}
            InputProps={{
                startAdornment: <InputAdornment position="start">$</InputAdornment>
            }} /> : null;
    let selectedTraining = null;

    switch(props.data.selected) {
        case 'cmi':
            selectedTraining = 
                <Fragment>
                    <Typography variant="h6" gutterBottom>CMI Fields</Typography>
                    <Typography variant="body1">IMI Level (Army / Others)</Typography>
                    <RadioGroup
                        name="cmi-level"
                        value={props.data.imiLevel}
                        onChange={props.onIMILvlChange}
                        row>
                        <FormControlLabel value="1" control={<Radio />} label="0 (1)"/>
                        <FormControlLabel value="2" control={<Radio />} label="1 (2)"/>
                        <FormControlLabel value="3" control={<Radio />} label="2 (3)"/>
                        <FormControlLabel value="4" control={<Radio />} label="3 (4)"/>
                        {/* <FormControlLabel value="0" control={<Radio />} label="Other"/> */}
                    </RadioGroup>
                    <Typography variant="caption" className={classes.note}>NOTE: The cost per IMI hour for levels above 0 (1) is determined by the base cost of one hour of level 0 (1) instruction.</Typography>
                    {otherIMICostField}
                    <TextField 
                        type="number" id="cmi-finishedIMIHours" fullWidth
                        className={classes.textField}
                        label={appText.cmi.fields.developedIMIHours.label}
                        onChange={props.onChanged} value={props.data.cmi.finishedIMIHours} />
                    {/* <TextField 
                        type="number" id="cmi-contractIMIHours" fullWidth
                        className={classes.textField}
                        label={appText.cmi.fields.contractIMIHours.label}
                        onChange={props.onChanged} value={props.data.cmi.contractIMIHours} /> */}
                    <TextField 
                        type="number" id="cmi-contentPackages" fullWidth
                        className={classes.textField}
                        label={appText.cmi.fields.contentPackages.label}
                        onChange={props.onChanged} value={props.data.cmi.contentPackages} />
                    <FormControlLabel 
                        label = "Enter Evaluation field values"
                        control = {
                            <Checkbox
                                id="cmi-enterEvalFields"
                                checked={props.data.cmi.enterEvalFields}
                                onChange={props.onCheckboxChanged}/>
                        }/>
                    {props.data.cmi.enterEvalFields ? 
                        <Fragment>
                            <Typography variant="h6" gutterBottom>Evaluation Fields (for future use)</Typography>
                            <TextField 
                                type="number" id="cmi-cmiAthdCost" fullWidth
                                className={classes.textField}
                                label={appText.shared.fields.evaluation.helpDeskCost.label}
                                onChange={props.onChanged} value={props.data.cmi.cmiAthdCost} />
                            <TextField 
                                type="number" id="cmi-cmiAthdLicenseCost" fullWidth
                                className={classes.textField}
                                label={appText.shared.fields.evaluation.helpDeskLicenseCost.label}
                                onChange={props.onChanged} value={props.data.cmi.cmiAthdLicenseCost} />
                            <TextField 
                                type="number" id="cmi-cmiTktHours" fullWidth
                                className={classes.textField}
                                label={appText.shared.fields.evaluation.tktHours.label}
                                onChange={props.onChanged} value={props.data.cmi.cmiTktHours} />
                            <TextField 
                                type="number" id="cmi-cmiDatrCost" fullWidth
                                className={classes.textField}
                                label={appText.shared.fields.evaluation.datrCost.label}
                                onChange={props.onChanged} value={props.data.cmi.cmiDatrCost} />
                        </Fragment> : null}
                </Fragment>
            break;
        case 'cai':
            selectedTraining = 
                <Fragment>
                    <Typography variant="h6" gutterBottom>CAI Fields</Typography>
                    <TextField
                        type="number" id="cai-courseLength" fullWidth
                        className={classes.textField}
                        label={appText.caiAndLive.fields.courseLength.label}
                        onChange={props.onChanged} value={props.data.cai.courseLength} />
                    <TextField
                        type="number" id="cai-TDYDays" fullWidth
                        className={classes.textField}
                        label={appText.caiAndLive.fields.tdyDays.label}
                        onChange={props.onChanged} value={props.data.cai.TDYDays} />
                    <TextField
                        type="number" id="cai-classroomsRequired" fullWidth
                        className={classes.textField}
                        label={appText.caiAndLive.fields.classroomsRequired.label}
                        onChange={props.onChanged} value={props.data.cai.classroomsRequired} />
                    <TextField
                        type="number" id="cai-instructorsRequired" fullWidth
                        className={classes.textField}
                        label={appText.caiAndLive.fields.studentsPerClassRoom.label}
                        onChange={props.onChanged} value={props.data.cai.instructorsRequired} />
                    <FormControlLabel 
                        label = "Enter Evaluation field values"
                        control = {
                            <Checkbox
                                id="cai-enterEvalFields"
                                checked={props.data.cai.enterEvalFields}
                                onChange={props.onCheckboxChanged}/>
                        }/>
                    {props.data.cai.enterEvalFields ? 
                        <Fragment>
                            <Typography variant="h6" gutterBottom>Evaluation Fields (for future use)</Typography>
                            <TextField 
                                type="number" id="cai-caiAthdCost" fullWidth
                                className={classes.textField}
                                label={appText.shared.fields.evaluation.helpDeskCost.label}
                                onChange={props.onChanged} value={props.data.cai.caiAthdCost} />
                            <TextField 
                                type="number" id="cai-caiAthdLicenseCost" fullWidth
                                className={classes.textField}
                                label={appText.shared.fields.evaluation.helpDeskLicenseCost.label}
                                onChange={props.onChanged} value={props.data.cai.caiAthdLicenseCost} />
                            <TextField 
                                type="number" id="cai-caiTktHours" fullWidth
                                className={classes.textField}
                                label={appText.shared.fields.evaluation.tktHours.label}
                                onChange={props.onChanged} value={props.data.cai.caiTktHours} />
                        </Fragment> : null}
                </Fragment>
            break;
        case 'live':
            selectedTraining =
                <Fragment>
                    <Typography variant="h6" gutterBottom>Live Fields</Typography>
                    <TextField
                        type="number" id="live-courseLength" fullWidth
                        className={classes.textField}
                        label={appText.caiAndLive.fields.courseLength.label}
                        onChange={props.onChanged} value={props.data.live.courseLength} />
                    <TextField
                        type="number" id="live-TDYDays" fullWidth
                        className={classes.textField}
                        label={appText.caiAndLive.fields.tdyDays.label}
                        onChange={props.onChanged} value={props.data.live.TDYDays} />
                    <TextField
                        type="number" id="live-classroomsRequired" fullWidth
                        className={classes.textField}
                        label={appText.caiAndLive.fields.classroomsRequired.label}
                        onChange={props.onChanged} value={props.data.live.classroomsRequired} />
                    <TextField
                        type="number" id="live-instructorsRequired" fullWidth
                        className={classes.textField}
                        label={appText.caiAndLive.fields.studentsPerClassRoom.label}
                        onChange={props.onChanged} value={props.data.live.instructorsRequired} />
                    <FormControlLabel 
                        label = "Enter Evaluation field values"
                        control = {
                            <Checkbox
                                id="live-enterEvalFields"
                                checked={props.data.live.enterEvalFields}
                                onChange={props.onCheckboxChanged}/>
                        }/>
                    {props.data.live.enterEvalFields ? 
                        <Fragment>
                            <Typography variant="h6" gutterBottom>Evaluation Fields (for future use)</Typography>
                            <TextField 
                                type="number" id="live-liveAthdCost" fullWidth
                                className={classes.textField}
                                label={appText.shared.fields.evaluation.helpDeskCost.label}
                                onChange={props.onChanged} value={props.data.live.liveAthdCost} />
                            <TextField 
                                type="number" id="live-liveAthdLicenseCost" fullWidth
                                className={classes.textField}
                                label={appText.shared.fields.evaluation.helpDeskLicenseCost.label}
                                onChange={props.onChanged} value={props.data.live.liveAthdLicenseCost} />
                            <TextField 
                                type="number" id="live-liveTktHours" fullWidth
                                className={classes.textField}
                                label={appText.shared.fields.evaluation.tktHours.label}
                                onChange={props.onChanged} value={props.data.live.liveTktHours} />
                        </Fragment> : null}
                    
                </Fragment>
            break;
        default:
            selectedTraining = null;
            break;
    }
    return selectedTraining;
        
}

export default withStyles(styles)(Fields);