import React, { Component } from 'react';

import CostCalculator from './containers/CostCalculator';
import CostCalculatorSheet from './containers/CostCalculatorSheet';

import { withStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import ToolBar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';

import appText from './data/appText';

const styles = theme => ({
  root: {
    flexGrow: 1
  },
  grow: {
    flexGrow: 1
  },
  button: {
    color: 'white'
  },
  paper: {
      padding: theme.spacing.unit * 2
  },
  textField: {
      marginBottom: theme.spacing.unit
  },
  divider: {
      marginTop: theme.spacing.unit * 2,
      marginBottom: theme.spacing.unit * 2
  }
})

class App extends Component {
  constructor() {
    super();

    this.state = {
      view: 'simple',
      selected: null,
      imiLevel: "1",
      shared : {
          learnerCostPerDay: 1.61,
          loadedAnnualInstructorCost: 100000,
          TDYCostPerDay: 100,
          learnerTargetAudience: 600000
      },
      cmi: {
          costPerIMIHour: 1,
          customCostPerIMIHour: 1,
          imiMultiplier: 42,
          finishedIMIHours: 1,
          contractIMIHours: 0,
          contentPackages: 1,
          cmiAthdCost: 0,
          cmiAthdLicenseCost: 0,
          cmiTktHours: 0,
          cmiDatrCost: 0,
          cmiEvaluationCostPerLearner: 0,
          enterEvalFields: false
      },
      cai: {
          courseLength: 0,
          TDYDays: 0,
          classroomsRequired: 0,
          instructorsRequired: 25,
          caiAthdCost: 0,
          caiAthdLicenseCost: 0,
          caiTktHours: 0,
          caiEvaluationCostPerLearner: 0,
          enterEvalFields: false
      },
      live: {
          courseLength: 0,
          TDYDays: 0,
          classroomsRequired: 0,
          instructorsRequired: 25,
          liveAthdCost: 0,
          liveAthdLicenseCost: 0,
          liveTktHours: 0,
          liveEvaluationCostPerLearner: 0,
          enterEvalFields: false
      }
    }
  }

  handleFieldChange = e => {
    let type = e.target.id.split('-')[0];
    let costPerIMIHour = this.state.cmi.costPerIMIHour;
    const IDNum = e.target.id.split('-')[1];

    if (type === 'cmi' && IDNum === 'customCostPerIMIHour') {
        costPerIMIHour = e.target.value;
    }

    this.setState({
        [type]: {
            ...this.state[type],
            [IDNum]: e.target.value,
            costPerIMIHour: costPerIMIHour
        }
    });
  }

  handleFieldCheckboxChange = e => {
    let type = e.target.id.split('-')[0];
    const IDNum = e.target.id.split('-')[1];
    this.setState({
        [type]: {
            ...this.state[type],
            [IDNum]: e.target.checked
        }
    });
  }

handleTrainingType = e => {
    this.setState({
        selected: e.target.value
    })
  }

handleIMILevelChange = e => {
    let customCostPerIMIHour = this.state.cmi.customCostPerIMIHour;
    let imiCostPerHour = this.state.cmi.costPerIMIHour;
    let imiMultiplier = 42;

    switch (e.target.value) {
        case "1":
            imiCostPerHour = customCostPerIMIHour;
            imiMultiplier = 42;
            break;
        case "2":
            imiCostPerHour = (customCostPerIMIHour * 1.6904).toFixed(2);
            imiMultiplier = 71;
            break;
        case "3":
            imiCostPerHour = (customCostPerIMIHour * 3.0952).toFixed(2);
            imiMultiplier = 130;
            break;
        case "4":
            imiCostPerHour = (customCostPerIMIHour * 3.4047).toFixed(2);
            imiMultiplier = 143;
            break;
        default:
            imiCostPerHour = 50000;
            break;
    }

    this.setState({
        imiLevel: e.target.value,
        cmi: {
            ...this.state.cmi,
            costPerIMIHour: imiCostPerHour,
            imiMultiplier: imiMultiplier
        }
    });
  }

  handleViewChange = e => {
      this.setState({
          view: e
      })
  }

  render() {
    const {classes} = this.props;

    const calcView = this.state.view === 'simple' ? 
      <CostCalculator 
        state={this.state}
        handleFieldChange={this.handleFieldChange}
        handleFieldCheckboxChange={this.handleFieldCheckboxChange}
        handleTrainingType={this.handleTrainingType}
        handleIMILevelChange={this.handleIMILevelChange}/> : 
      <CostCalculatorSheet 
        state={this.state}
        handleFieldChange={this.handleFieldChange}
        handleFieldCheckboxChange={this.handleFieldCheckboxChange}
        handleTrainingType={this.handleTrainingType}
        handleIMILevelChange={this.handleIMILevelChange}/>

    return (
      <div className={classes.root}>
        <CssBaseline/>
        <AppBar position="static">
          <ToolBar>
            <Typography variant="h4" style={{color: 'white'}} className={classes.grow}>
              {appText.shared.appTitle} {appText.shared.appVersion}
            </Typography>
            <Button 
                className={classes.button} 
                style={this.state.view === 'simple' ? {backgroundColor: 'rgba(0, 0, 0, 0.2)'} : null}
                onClick={() => this.handleViewChange('simple')}>Simplified View</Button>
            <Button 
                className={classes.button} 
                style={this.state.view === 'sheet' ? {backgroundColor: 'rgba(0, 0, 0, 0.2)'} : null}
                onClick={() => this.handleViewChange('sheet')}>Spreadsheet View</Button>
          </ToolBar>
        </AppBar>
        {calcView}
      </div>
    );
  }
}

export default withStyles(styles)(App);
