import React, { Component, Fragment } from 'react';

import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell'
import TextField from '@material-ui/core/TextField';
import Divider from '@material-ui/core/Divider';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';

import appText from '../../../data/appText';

const styles = theme => ({
    root: {
        padding: theme.spacing.unit * 2,
        width: '100%'
    },
    paper: {
        padding: theme.spacing.unit * 2
    },
    tableRow: {
        height: '40px'
    },
    tableCell: {
        borderBottom: 0,
        padding: 0,
        textAlign: 'left'
    },
    divider: {
        marginTop: theme.spacing.unit * 2,
        marginBottom: theme.spacing.unit * 2
    }
});

class CAI extends Component {
    constructor(props) {
        super(props);
        this.data = {
            ...this.props.data.shared,
            ...this.props.data.cai,
            totalTDYCost: 0,
            instPer25Students: 0,
            instTeaching40weeks1: 0,
            instTeaching40weeks2: 0,
            instTDY3Years: 0,
            instAnnualCost: 0,
            learnerTDYCost: 0,
            instCostPerAnnualLearner: 0,
            costOneClassroom: 0,
        };
    }

    calcAndSetData() {
        let workingData = { ...this.data, ...this.props.data.shared, ...this.props.data.cai };

        let calculations = {
            instPer25Students: workingData.learnerTargetAudience / workingData.instructorsRequired,
            instTeaching40weeks1: (workingData.learnerTargetAudience / 25) / 20,
            instTeaching40weeks2: (workingData.learnerTargetAudience / 25) / 40,
            instTDY3Years: ((workingData.learnerTargetAudience / 25) / 20) / 3,
            instAnnualCost: (((workingData.learnerTargetAudience / 25) / 20) / 3) * workingData.loadedAnnualInstructorCost,
            instCostPerAnnualLearner: 
                ((((workingData.learnerTargetAudience / 25) / 20) / 3) * workingData.loadedAnnualInstructorCost) 
                / workingData.learnerTargetAudience / 3,
            costOneClassroom: workingData.learnerCostPerDay * workingData.TDYDays * workingData.classroomsRequired,
            implementationCostPerLearner: 
                workingData.learnerTDYCost
                + (((((workingData.learnerTargetAudience / 25) / 20) / 3) * workingData.loadedAnnualInstructorCost) 
                    / workingData.learnerTargetAudience / 3)
                + (workingData.learnerCostPerDay * workingData.TDYDays * workingData.classroomsRequired),
            athdCost: Number(workingData.caiAthdCost),
            athdLicenseCost: Number(workingData.caiAthdLicenseCost),
            tktHours: workingData.caiTktHours,
            EvaluationCostPerLearner: (Number(workingData.caiAthdCost) + Number(workingData.caiAthdLicenseCost)) / workingData.learnerTargetAudience,
            totalCost: 
                ((((workingData.learnerTargetAudience / 25) / 20) / 3) * workingData.loadedAnnualInstructorCost)
                + (workingData.learnerTargetAudience * workingData.learnerTDYCost)
                + (Number(workingData.caiAthdCost) + Number(workingData.caiAthdLicenseCost)),
            totalCostPerLearner:
                workingData.learnerTDYCost
                + (((((workingData.learnerTargetAudience / 25) / 20) / 3) * workingData.loadedAnnualInstructorCost) 
                    / workingData.learnerTargetAudience / 3)
                + (workingData.learnerCostPerDay * workingData.TDYDays * workingData.classroomsRequired)
                + (Number(workingData.caiAthdCost) + Number(workingData.caiAthdLicenseCost)) / workingData.learnerTargetAudience
        }
        
        this.data = { ...workingData, ...calculations }
    }

    render() {
        const { classes } = this.props;
        this.calcAndSetData();

        return (
            <Paper className={classes.paper}>
                <Typography variant="h6" gutterBottom>{appText.cai.title}</Typography>
                <Typography variant="body1"gutterBottom>{appText.cai.description}</Typography>
                <Divider className={classes.divider} />
                <Table>
                    <TableBody>
                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <Typography variant="h6">CAI Data Fields</Typography>
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.caiAndLive.fields.courseLength.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                <TextField
                                    type="number" id="cai-courseLength" fullWidth
                                    className={classes.textField}
                                    onChange={this.props.handleFieldChange} value={this.data.courseLength} />
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.caiAndLive.fields.tdyDays.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                <TextField
                                    type="number" id="cai-TDYDays" fullWidth
                                    className={classes.textField}
                                    onChange={this.props.handleFieldChange} value={this.data.TDYDays} />
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.caiAndLive.fields.classroomsRequired.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                <TextField
                                    type="number" id="cai-classroomsRequired" fullWidth
                                    className={classes.textField}
                                    onChange={this.props.handleFieldChange} value={this.data.classroomsRequired} />
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.caiAndLive.fields.studentsPerClassRoom.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                <TextField
                                    type="number" id="cai-instructorsRequired" fullWidth
                                    className={classes.textField}
                                    onChange={this.props.handleFieldChange} value={this.data.instructorsRequired} />
                            </TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <FormControlLabel 
                                    label = "Enter Evaluation field values"
                                    control = {
                                        <Checkbox
                                            id="cai-enterEvalFields"
                                            checked={this.data.enterEvalFields}
                                            onChange={this.props.handleFieldCheckboxChange}/>
                                    }/>
                            </TableCell>
                        </TableRow>
                        {this.data.enterEvalFields ? 
                            <Fragment>
                                <TableRow className={classes.tableRow}>
                                    <TableCell colSpan={2} className={classes.tableCell}>
                                        <Typography variant="h6">Evaluation Fields (for future use)</Typography>
                                    </TableCell>
                                </TableRow>
                                <TableRow className={classes.tableRow}>
                                    <TableCell className={classes.tableCell}>
                                        {appText.shared.fields.evaluation.helpDeskCost.label}
                                    </TableCell>
                                    <TableCell className={classes.tableCell}>
                                        <TextField 
                                            type="number" id="cai-caiAthdCost" fullWidth 
                                            className={classes.textField}
                                            onChange={this.props.handleFieldChange} value={this.data.caiAthdCost} />
                                    </TableCell>
                                </TableRow>
                                <TableRow className={classes.tableRow}>
                                    <TableCell className={classes.tableCell}>
                                        {appText.shared.fields.evaluation.helpDeskLicenseCost.label}
                                    </TableCell>
                                    <TableCell className={classes.tableCell}>
                                        <TextField 
                                            type="number" id="cai-caiAthdLicenseCost" fullWidth
                                            className={classes.textField}
                                            onChange={this.props.handleFieldChange} value={this.data.caiAthdLicenseCost} />
                                    </TableCell>
                                </TableRow>
                                <TableRow className={classes.tableRow}>
                                    <TableCell className={classes.tableCell}>
                                        {appText.shared.fields.evaluation.tktHours.label}
                                    </TableCell>
                                    <TableCell className={classes.tableCell}>
                                        <TextField 
                                            type="number" id="cai-caiTktHours" fullWidth
                                            className={classes.textField}
                                            onChange={this.props.handleFieldChange} value={this.data.caiTktHours} />
                                    </TableCell>
                                </TableRow>
                                <TableRow className={classes.tableRow}>
                                    <TableCell className={classes.tableCell} style={{}}></TableCell>
                                    <TableCell className={classes.tableCell} style={{}}></TableCell>
                                </TableRow>
                            </Fragment> : null}
                            
                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <Divider className={classes.divider}/>
                                <Typography variant="h5" gutterBottom>Costs by ADDIE Stage</Typography>
                                <Typography variant="h6" gutterBottom>{appText.misc.analysis.title}</Typography>   
                                <Typography variant="body1" gutterBottom>{appText.misc.analysis.text}</Typography>  
                            </TableCell>
                        </TableRow>

                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <Typography variant="h6">{appText.misc.design.title}</Typography>   
                                <Typography variant="body1" gutterBottom>{appText.misc.design.text}</Typography>
                            </TableCell>
                        </TableRow>

                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <Typography variant="h6">{appText.misc.development.title}</Typography>
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                {appText.misc.development.text}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}></TableCell>
                            <TableCell className={classes.tableCell}></TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}></TableCell>
                            <TableCell className={classes.tableCell}></TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}></TableCell>
                            <TableCell className={classes.tableCell}></TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}></TableCell>
                            <TableCell className={classes.tableCell}></TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}></TableCell>
                            <TableCell className={classes.tableCell}></TableCell>
                        </TableRow>

                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <Typography variant="h6">{appText.misc.implementation.title}</Typography>
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.caiAndLive.implementation.instructorsRequired.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                {Math.round(this.data.instPer25Students).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.caiAndLive.implementation.instructors40Weeks.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                {Math.round(this.data.instTeaching40weeks1).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.caiAndLive.implementation.instructors3Years.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                {Math.round(this.data.instTDY3Years).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.caiAndLive.implementation.annualInstructorCost.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.instAnnualCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.caiAndLive.implementation.instructorCost3Years.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.instCostPerAnnualLearner.toFixed(2)}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.caiAndLive.implementation.totalLearnerTDYCost.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.totalTDYCost.toFixed(2)}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.caiAndLive.implementation.tdyCostPerLearner.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.learnerTDYCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.caiAndLive.implementation.classroomCostPerLearner.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.costOneClassroom.toFixed(2)}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.caiAndLive.implementation.costPerLearner.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.implementationCostPerLearner.toFixed(2)}
                            </TableCell>
                        </TableRow>

                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <Typography variant="h6">{appText.misc.evaluation.title}</Typography>
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.shared.fields.evaluation.helpDeskCost.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.athdCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.shared.fields.evaluation.helpDeskLicenseCost.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.athdLicenseCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.shared.fields.evaluation.tktHours.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                {this.data.tktHours.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cmi.evaluation.costPerLearner.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.EvaluationCostPerLearner.toFixed(4)}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}></TableCell>
                            <TableCell className={classes.tableCell}></TableCell>
                        </TableRow>                        
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}></TableCell>
                            <TableCell className={classes.tableCell}></TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell colSpan={2} className={classes.tableCell}>
                                <Typography variant="h6">{appText.misc.totals.title}</Typography>
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cai.totals.cost.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.totalCost.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                        <TableRow className={classes.tableRow}>
                            <TableCell className={classes.tableCell}>
                                {appText.cai.totals.costPerLearner.label}
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                                ${this.data.totalCostPerLearner.toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            </TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
            </Paper>
        );
    }
}

export default withStyles(styles)(CAI);